.feedback-form {
    display: flex;
    flex-direction: column;
    max-width: 480px;
    padding: 12px;
    background-color: #fff;
    border: 1px solid rgba(0, 0, 0, 0.12);
    border-radius: 4px;
}

.feedback-form label {
    display: flex;
    flex-direction: column;
    margin-bottom: 16px;
}

.feedback-form button {
    border-radius: 4px;
    padding: 8px 12px;
    font: inherit;
    cursor: pointer;
}

input,
textarea {
    border-radius: 4px;
    padding: 8px;
    font: inherit;
}


input:focus {
    border: 3px solid #5ab95d;
    outline: none;
}


textarea:focus {
    border: 3px solid #5ab95d;
    outline: none;
}

textarea {
    resize: none;
}

.submit {
    /* transition: 0.2s; */
}

.submit:hover {
    border: none;
    background-color: #5ab95d;
    /* transform: translateY(-3px); */
    /* box-shadow: 0 7px 0 darkgreen; */
}

.submit:active {
    /* transform: translateY(-1px); */
    box-shadow: 0 0 6px 1px darkgreen;

}