* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.feedback-form {
  max-width: 480px;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, .12);
  border-radius: 4px;
  flex-direction: column;
  padding: 12px;
  display: flex;
}

.feedback-form label {
  flex-direction: column;
  margin-bottom: 16px;
  display: flex;
}

.feedback-form button {
  font: inherit;
  cursor: pointer;
  border-radius: 4px;
  padding: 8px 12px;
}

input, textarea {
  font: inherit;
  border-radius: 4px;
  padding: 8px;
}

input:focus, textarea:focus {
  border: 3px solid #5ab95d;
  outline: none;
}

textarea {
  resize: none;
}

.submit:hover {
  background-color: #5ab95d;
  border: none;
}

.submit:active {
  box-shadow: 0 0 6px 1px #006400;
}

